import auth0 from 'auth0-js'
import history from './History'

class Auth {
  constructor() {

  this.auth0 = new auth0.WebAuth({
      // the following three lines MUST be updated
    domain: 'dev-0r9j-iz3.auth0.com',
    audience: 'https://dev-0r9j-iz3.auth0.com/userinfo',
    clientID: '1HbLQ6nRcscQnN2UY5i79et3CRBQq3zQ',
    redirectUri: 'https://mmarsden89.github.io/national-park-prototype/callback',
    responseType: 'id_token',
    scope: 'openid profile'
  })

  this.getProfile = this.getProfile.bind(this)
  this.handleAuthentication = this.handleAuthentication.bind(this)
  this.isAuthenticated = this.isAuthenticated.bind(this)
  this.signIn = this.signIn.bind(this)
  this.signOut = this.signOut.bind(this)

}

  getProfile() {
    return this.profile
  }

  getIdToken() {
    return this.idToken
  }

  isAuthenticated() {
    let expiresAt = JSON.parse(localStorage.getItem('expires_at'))
    return new Date().getTime() < this.expiresAt &&
    new Date().getTime() < expiresAt
  }

  signIn() {
    this.auth0.authorize()
  }

  handleAuthentication() {
      this.auth0.parseHash((err, authResult) => {
        if (authResult) {
          this.setSession(authResult)
        } else if (err) {
        history.replace("/")
      }
      })
      history.replace('/')
  }

  setSession(authResult) {
    this.idToken = authResult.idToken
    this.profile = authResult.idTokenPayload
    this.accessToken = authResult.accessToken
    // set the time that the id token will expire at
    this.expiresAt = authResult.idTokenPayload.exp * 1000
    localStorage.setItem('profile', authResult.idTokenPayload)
    localStorage.setItem('access_token', authResult.accessToken);
    localStorage.setItem('id_token', authResult.idToken);
    localStorage.setItem('expires_at', authResult.idTokenPayload.exp * 1000);
  }

  signOut() {
    this.auth0.logout({
      returnTo: 'https://mmarsden89.github.io/national-park-prototype',
      clientID: '1HbLQ6nRcscQnN2UY5i79et3CRBQq3zQ',
    })
    // navigate to the home route
    localStorage.removeItem('access_token');
    localStorage.removeItem('id_token');
    localStorage.removeItem('expires_at');
    history.replace('/')
  }

  silentAuth() {
    return new Promise((resolve, reject) => {
      this.auth0.checkSession({}, (err, authResult) => {
        if (err) return reject(err)
        this.setSession(authResult)
        resolve()
      })
    })
  }
}

const auth = new Auth()

export default auth
