import React, {Component} from 'react';
import {withRouter} from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSignOutAlt, faSignInAlt } from '@fortawesome/free-solid-svg-icons'

class NavBar extends Component {
  constructor(props) {
    super(props)
    this.state = {
      authenticated: false
    }
  }
  componentDidMount () {
    console.log(this.props.auth)
    if (this.props.auth.isAuthenticated()) {
      this.setState({ authenticated: true})
    }
  }

  shouldComponentUpdate(nextProps, nextState) {
    return nextState !== this.state || nextProps !== this.props
  }

  signOut() {
    this.props.auth.signOut();
  }

  render() {
    return (
      <nav className="navbar">
        <div className="navbar-container">
          {
            !this.props.auth.isAuthenticated() &&
            <div className="profile-area">
            <FontAwesomeIcon icon={faSignInAlt} className="signout" onClick={this.props.auth.signIn}/>
            <p className="nickname">login</p>
            </div>
          }
          {
            this.props.auth.isAuthenticated() &&
            <div className="profile-area">
              <p className="nickname">{this.props.auth.profile.nickname || 'welcome!'}</p>
              <FontAwesomeIcon icon={faSignOutAlt} onClick={() => {this.props.auth.signOut()}} className="signout"/>
            </div>
          }
        </div>
        <div className="gradient">
        </div>
      </nav>
    )
  }
}

export default withRouter(NavBar);
